/**
 * Used for static pages like login, logout, unsupported-browser and noscript section.
 * Note: This box must not depend on javascript because it is used for the noscript section as well.
 */
.box {
  display: table;
  position: absolute;
  height: 75%;
  width: 100%;
  margin: 15px 0;
}
.box > .wrapper {
  display: table-cell;
  vertical-align: middle;
}
.box-content {
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 12px;
  background-color: #2E3C41;
  max-width: 450px;
  margin: 0 auto;
  padding: 45px 95px 50px 95px;
  box-shadow: 0 20px 50px 0 rgba(0, 0, 0, 0.2);
  font-size: 14px;
}
.box-content > .header {
  text-align: center;
}
.box-content > .header > .logo {
  margin-top: 4px;
  margin-bottom: 40px;
  max-height: 30px;
}
.box-content.small > .header > .logo {
  max-width: 50%;
}
.box-content > .button-bar {
  padding-top: 32px;
  text-align: center;
}
.box-content > .button-bar > .button {
  padding: 7px 7px;
  font-size: 14px;
}
.box-content.centered {
  text-align: center;
}
@media (max-width: 490px) {
  .box-content {
    padding: 40px;
    margin-left: 10px;
    margin-right: 10px;
  }
  .box-content > .header > .logo {
    margin-bottom: 30px;
    margin-top: 0;
  }
}
@media (max-height: 400px) {
  .box-content {
    padding-top: 25px;
    padding-bottom: 25px;
  }
  .box-content > .header > .logo {
    margin-bottom: 20px;
    margin-top: 0;
  }
}
@media (max-height: 700px) {
  .box {
    height: calc(100% -  15px * 2);
  }
}
