.switch {
  position: absolute;
  right: 0;
  display: flex;
}
.switch > .switch-button {
  margin-top: -2px;
}
.switch-label {
  margin-right: 8px;
}
.switch-button,
.switch-button:before {
  transition: 0.25s ease-in-out;
}
.switch-button {
  width: 36px;
  position: relative;
  height: 22px;
  border-radius: 11px;
  background-color: rgba(0, 0, 0, 0.6);
  cursor: pointer;
  border: solid 1px #7E8588;
  --activated-border-color: #71b0e8;
  --activated-background-color: #71b0e8;
}
.switch-button:before {
  content: '';
  display: block;
  position: absolute;
  height: 16px;
  width: 16px;
  border-radius: 50%;
  top: 2px;
  left: 2px;
  border: solid 1px #E9EBEB;
  background-color: #E9EBEB;
}
.switch-button.activated {
  background-color: var(--activated-background-color);
  border-color: var(--activated-border-color);
}
.switch-button.activated:before {
  background-color: #1F2428;
  border-color: #1F2428;
  left: calc(100% - 18px);
}
.disabled > .switch-button {
  background-color: transparent;
  border-color: rgba(255, 255, 255, 0.12);
  cursor: unset;
}
.disabled > .switch-button::before {
  background-color: rgba(255, 255, 255, 0.12);
  border-color: rgba(255, 255, 255, 0.12);
}
.disabled > .switch-button.activated {
  background-color: #7E8588;
  border-color: #7E8588;
}
.disabled > .switch-button.activated::before {
  background-color: #1F2428;
  border-color: #1F2428;
}
