.widget-tooltip {
  max-width: none;

  & > .tooltip-widget-container {
    position: relative;
    padding: 10px;
    background-color: inherit;
    overflow: hidden;
    border-radius: @tooltip-border-radius;

    & > .widget {
      position: relative;
    }
  }
}
